import Button from '@mui/material/Button';
import LeftMenu from '../LeftMenu';
import { useState } from "react";
import MenuIcon from '@mui/icons-material/Menu';

const ConnectWallet = () => {
    const [visible, setVisible] = useState(false);
    const onClose = () => {
        console.log("close");
        setVisible(false);
    }
    const onTurn = () => {
        setVisible(true);
    }
    return (
        <>
            <div className='wallet'>
                <Button className="btn_menu" variant="text" onClick={onTurn}> <MenuIcon></MenuIcon> </Button>
                <Button className="btn_connect" variant="contained">Connect</Button>
            </div>
            {
                visible == true ?
                    <div style={{ position: "fixed", top: "0px", bottom: "0px", left: "0px", right: "0px", zIndex: 10000, background: "rbga(10,10,10,0.5)" }}
                        onClick={onClose}>
                        <LeftMenu></LeftMenu>
                    </div> : <></>

            }
        </>
    );
}

export default ConnectWallet;