import ConnectWallet from "../Common/ConnetWallet";
import { CustomSelect, StyledOption } from "../Common/CustomSelect";
import { useState } from "react";
import Button from '@mui/material/Button';

const Swap = () => {
    const [value, setValue] = useState(0);
    return (
        <div className="home_container">
            <ConnectWallet />
            <div className="m-t-40">
                <div className="market_cap_container">
                    <div className="market_cap flex1">
                        <div className="content">
                            <div className="c-w fs-14">EPI Market Cap</div>
                            <div className="flex justify-between align-center">
                                <span className="c-w fs-24 market_cap_chart fw-700">
                                    $20,000,000
                                </span>
                                <img style={{ width: "43px", height: "21px" }} src="/img/chart_line.png" />
                            </div>
                            <div className="c-w fs-14">
                                +10.25%
                            </div>
                        </div>
                        <div className="gray_bar_vert"></div>
                        <div className="content">
                            <div className="c-w fs-14">EPI Market Cap</div>
                            <div className="flex justify-between align-center">
                                <span className="c-w fs-24 market_cap_chart fw-700">
                                    $20,000,000
                                </span>
                                <img style={{ width: "43px", height: "21px" }} src="/img/chart_line.png" />
                            </div>
                            <div className="c-w fs-14">
                                +10.25%
                            </div>
                        </div>
                    </div>
                    <div style={{ width: "40px" }}></div>
                    <div className="check_nft flex1">
                        <div className="content">
                            <div className="fs-24 c-w fw-700">
                                CONNECT OUR
                            </div>
                            <div className="fs-24 c-w fw-700">
                                NFTS
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="swap_pannel flex">
                <div className="content">
                    <div className="c-w fs-14">EPI Balance</div>
                    <div className="flex justify-between align-center">
                        <span className="c-w fs-24 market_cap_chart fw-700">
                            $20,000,000
                        </span>
                    </div>

                </div>
                <div className="gray_bar_vert"></div>
                <div className="content">
                    <div className="c-w fs-14">EPI Balance</div>
                    <div className="flex justify-between align-center">
                        <span className="c-w fs-24 market_cap_chart fw-700">
                            $20,000,000
                        </span>
                    </div>
                </div>
            </div>
            <div className="swap_ctrl_pannel">
                <div className="swap_select flex justify-between align-center">
                    <div className="h-full flex flex-col justify-between">
                        <div className="fs-24 c-w fw-700">0.00</div>
                        <div className="fs-14 c-w m-t-15">Balance: 0.00</div>
                    </div>
                    <CustomSelect value={value} onChange={setValue}>
                        <StyledOption value={10}>MATIC</StyledOption>
                        <StyledOption value={20}>EPI</StyledOption>
                        <StyledOption value={30}>Thirty</StyledOption>
                    </CustomSelect>
                </div>
                <div className="m-t-15 m-b-15 flex justify-center">
                    <img className="cursor-pointer" src="/img/swap.png" />
                </div>
                <div className="swap_select flex justify-between align-center">
                    <div className="h-full flex flex-col justify-between">
                        <div className="fs-24 c-w fw-700">0.00</div>
                        <div className="fs-14 c-w m-t-15">Balance: 0.00</div>
                    </div>
                    <CustomSelect value={value} onChange={setValue}>
                        <StyledOption value={10}>MATIC</StyledOption>
                        <StyledOption value={20}>EPI</StyledOption>
                        <StyledOption value={30}>Thirty</StyledOption>
                    </CustomSelect>
                </div>
                <div className="m-t-40">
                    <Button className="btn_swap" variant="contained">
                        SWAP
                    </Button>
                </div>
            </div>
        </div>
    )
};

export default Swap;