import { useState } from "react";
import { Link, useHistory } from "react-router-dom";


const navList = [
    { id: 0, text: "Home", icon: "/img/ico_home.png", url: "/home" },
    { id: 1, text: "Champs Defi Radio Swap", icon: "/img/ico_swap.png", url: "/swap" },
    { id: 2, text: "Champs Defi Radio NFTS", icon: "/img/ico_chart.png", url: "/nft" },
    { id: 3, text: "Roadmap", icon: "/img/ico_location.png", url: "roadmap" }
]

const LeftMenu = () => {
    const [activeIndex, setActiveIndex] = useState(0);
    const history = useHistory();
    const onLink = (index) => {
        history.push(navList[index].url);
        setActiveIndex(index);
    }

    return (
        <div className="left_sider">
            <div className="sider_logo">
                <img className="logo" alt="logo" src="/champs-logo.png"></img>
                Champs Defi Radio
            </div>
            <div>
                <ul className="nav_container">
                    {
                        navList.map((item, index) => {
                            return <li key={index} className={activeIndex == index ? "nav active" : "nav"} onClick={() => { onLink(index) }}>
                                <img className="nav_icon" src={item.icon} />{item.text}
                            </li>;
                        })
                    }
                </ul>
            </div>
            <div className="gray_bar"></div>
            <div className="nav_social_container">
                <div className="nav_social_text">Social</div>
                <div className="nav_social_icon">
                    <img src="/img/twitter.png" />
                    <img src="/img/instagram.png" />
                </div>
            </div>
            <div className="flex1"></div>
            <div className="nav_paper_container">
                <div className="nav_paper">
                    Download Emp-paper <br />
                    Version 7.0
                </div>
            </div>
        </div>
    );
}


export default LeftMenu;